.stages {
    padding-bottom: 64px;
    position: relative;
    &__tabs {
        margin-top: -24px;
        border-radius: 6px;
        background-color: var(--color-white);
        box-shadow: 0 6px 12px rgba(0, 0, 0, 0.08);
        display: flex;
        align-items: center;
        justify-content: space-between;
        overflow: auto;
        gap: 33px;
        &-btn {
            position: relative;
            font-size: 12px;
            padding: 18px 51px;
            font-weight: 700;
            text-transform: uppercase;
            text-align: center;
            color: #99a3c5;
            transition: color 0.2s;
            &:hover {
                color: var(--color-dakr-gray);
                &:after {
                    height: 3px;
                }
            }
            &:after {
                @include pseudo();
                position: absolute;
                bottom: 0;
                left: 0;
                width: 100%;
                height: 0;
                transition: height 0.3s;
                background-color: #588cf4;
            }
            &--active {
                color: var(--color-dakr-gray);
                &:after {
                    height: 3px;
                }
            }
        }
    }
    &__container {
        margin-right: 0;
        margin-left: 396px;
    }
}

.author {
    display: flex;
    align-items: center;
    justify-content: space-between;
    &-left {
        display: flex;
        align-items: center;
    }
    &__image {
        display: block;
        border-radius: 16px;
        width: 40px;
        height: 40px;
        margin-right: 12px;
        filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.16));
    }
    &__name {
        display: block;
        color: var(--color-drak-gray);
        font-size: 14px;
        max-width: 125px;
        line-height: 17px;
    }
    &__desc {
        font-size: 14px;
        margin-left: 20px;
        color: var(--color-middle-gray);
    }
}

.stage {
    &__bottom {
        display: grid;
        align-items: flex-start;
        grid-template-columns: repeat(12, 1fr);
        gap: 24px;
    }
    &__left {
        grid-column: 4 span;
        padding: 45px 32px;
        background-color: var(--color-white);
        box-shadow: 0 6px 12px rgba(0, 0, 0, 0.08);
        border-radius: 8px;
        &-top {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 32px;
        }
        &-title {
            font-size: 18px;
        }
        &-btn {
            .more__icon {
                fill: var(--color-light-dark);
            }
        }
        &-info {
            display: flex;
            align-items: center;
            justify-content: center;
            margin-bottom: 24px;
            &__item {
                display: flex;
                flex-direction: column;
                align-items: center;
                padding: 0 28px;
                &:not(:last-child) {
                    border-right: 1px solid var(--color-super-light);
                }
            }
            &__image {
                padding: 7px;
                border-radius: 5px;
                margin-bottom: 11px;
                &--1 {
                    background-color: var(--color-purple);
                }
                &--2 {
                    background-color: var(--color-purple2);
                }
                svg {
                    display: block;
                    width: 27px;
                    height: 27px;
                }
            }
            &__desc {
                font-size: 13px;
                font-weight: 400;
                white-space: nowrap;
                color: var(--color-middle-gray);
                span {
                    font-weight: 700;
                    color: var(--color-dark-gray);
                }
            }
        }
        &-members {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 40px;
            &__left {
                display: flex;
                align-items: center;
            }
            &__image {
                display: block;
                border-radius: 16px;
                width: 40px;
                height: 40px;
                filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.16));
                &:not(:first-child) {
                    margin-left: -15px;
                }
            }
            &__more {
                display: flex;
                align-items: center;
                justify-content: center;
                color: var(--color-white);
                font-family: "Commissioner", sans-serif;
                font-weight: 700;
                margin-left: -15px;
                z-index: 1;
                width: 40px;
                height: 40px;
                border-radius: 16px;
                font-size: 12px;
                background-color: var(--color-dark-gray);
            }
            &__desc {
                font-size: 14px;
                color: var(--color-middle-gray);
            }
        }
    }
    &__right {
        display: flex;
        flex-direction: column;
        align-items: center;
        grid-column: 8 span;
        padding: 0 32px;
        color: var(--color-middle-gray);
        background-color: var(--color-white);
        box-shadow: 0 6px 12px rgba(0, 0, 0, 0.08);
        border-radius: 8px;
    }
    &__author {
        margin-bottom: 30px;
    }
    &__progressbar {
        &-line {
            height: 8px;
            box-shadow: none;
            &::-webkit-progress-bar {
                background: #EBEFF2;
                border-radius: 12px;
            }
        }
        &-bottom {
            margin-bottom: 8px;
            span {
                color: var(--color-middle-gray);
            }
            svg {
                fill: var(--color-middle-gray);
            }
        }
        &:not(:last-child) {
            margin-bottom: 24px;
        }
    }
    &__item {
        width: 100%;
        padding: 24px 0;
        border-bottom: 1px solid var(--color-super-light);
        color: var(--color-middle-gray);
        &-top {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 17px;
        }
        &-title {
            color: var(--color-super-dark);
            max-width: 320px;
        }
        &-save {
            svg {
                display: block;
                width: 14px;
                height: 20px;
                fill: var(--color-super-light);
                transition: fill 0.2s;
            }
            &--active {
                svg {
                    fill: var(--color-yellow);
                }
            }
        }
        &-author {
            justify-content: flex-start;
            margin-right: 72px;
        }
        &-body {
            display: flex;
            align-items: center;
            justify-content: flex-end;
        }
        &-status {
            text-align: center;
            color: var(--color-white);
            font-weight: 700;
            font-size: 12px;
            min-width: 80px;
            padding: 4px 0;
            border-radius: 2px;
        }
        &-footer {
            display: flex;
            align-items: center;
        }
        &-priority {
            margin-right: 20px;
        }
        &-comments {
            display: flex;
            align-items: center;
            font-size: 12px;
            margin-right: 12px;
            svg {
                display: block;
                width: 13px;
                height: 14px;
                margin-right: 5px;
            }
        }
        &-time {
            font-size: 12px;
        }
    }
    &__create {
        display: flex;
        align-items: center;
        font-family: "Commissioner", sans-serif;
        font-weight: 600;
        font-size: 12px;
        color: var(--color-middle-gray);
        text-transform: uppercase;
        letter-spacing: 0.01em;
        margin: 22px 0;
        svg {
            display: block;
            width: 24px;
            height: 24px;
            margin-right: 10px;
        }
    }
}

.tabs {
    &__content {
        display: none;
        &--active {
            display: block;
        }
    }
}

.status {
    &-work {
        background-color: var(--color-green);
    }
    &-new {
        background-color: var(--color-blue);
    }
    &-check {
        background-color: var(--color-orange);
    }
}

.priority {
    font-size: 12px;
    padding: 5px 12px;
    border-radius: 4px;
    &-3 {
        background-color: #F4DDEA;
        color: #AA3E86;
    }
    &-4 {
        background-color: #EDE8FE;
        color: #3C1987;
    }
    &-6 {
        background-color: #C1E0EB;
        color: #2B69BE;
    }
    &-7 {
        background-color: #FFE0CC;
        color: #BF5917;
    }
}