.dropdown {
    position: absolute;
    background-color: var(--color-white);
    color: var(--color-middle-gray);
    padding: 20px;
    border-radius: 6px;
    box-shadow: 0 15px 30px rgba(0, 0, 0, 0.08);
    opacity: 0;
    z-index: 100;
    visibility: hidden;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
    &:before {
        @include pseudo();
        position: absolute;
        top: -16px;
        right: 28px;
        width: 32px;
        height: 16px;
        background-image: url(../img/svg/triangle.svg);
        background-size: cover;
        background-repeat: no-repeat;
    }
    &__item {
        &:not(:last-child) {
            padding-bottom: 12px;
            margin-bottom: 12px;
            border-bottom: 1px solid #DFE8FF;
        }
    }
    &__link {
        white-space: nowrap;
        display: flex;
        align-items: center;
        font-size: 14px;
        transition: color 0.1s;
        &:hover {
            color: var(--color-accent);
            svg {
                fill: var(--color-accent)
            }
        }
        svg {
            display: block;
            width: 16px;
            height: 16px;
            margin-right: 10px;
            fill: #788CA1;
            transition: fill 0.1s;
        }
    }
}