.hero-stages {
    background: linear-gradient(0deg, rgba(255, 255, 255, 0.15), rgba(255, 255, 255, 0.15)), linear-gradient(180deg, #2393FF 0%, #35A3FB 100%);
    &__container {
        padding-top: 60px;
        padding-bottom: 76px;
    }
}

.hero-stage {
    &__item {
        display: grid;;
        color: var(--color-white);
        column-gap: 24px;
        grid-template-columns: repeat(12, 1fr);
    }
    &__image {
        max-width: calc(100% - 24px);
        display: block;
        grid-column: 2 span;
        padding: 48px 12px;
        background-color: var(--color-white);
        object-fit: cover;
        border-radius: 8px;
    }
    &__left {
        grid-column: 5 span;
    }
    &__right {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        justify-content: space-between;
        grid-column: 5 span;
        padding-top: 9px;
    }
    &__info {
        display: flex;
        align-items: center;
        gap: 20px;
        justify-content: space-between;
        &-item {
            display: flex;
            align-items: center;
        }
        &-text {
            display: flex;
            flex-direction: column;
            margin-left: 10px;
        }
        &-name {
            white-space: nowrap;
            font-weight: 500;
            font-size: 18px;
        }
        &-desc {
            white-space: nowrap;
            font-size: 14px;
            letter-spacing: 0.01em;
            font-weight: 500;
            color: rgba(#fff, 0.8);
        }
        &-image {
            padding: 11px;
            border-radius: 5px;
            background-color: var(--color-white);
            svg {
                display: block; 
                width: 18px;
                height: 18px;
            }
        }
    }
    &__title {
        font-weight: 700;
        font-size: 24px;
        line-height: 32px;
        margin-bottom: 17px;
    }
    &__map {
        display: flex;
        font-style: normal;
        align-items: center;
        background-image: url(../img/svg/map.svg);
        background-repeat: no-repeat;
        background-size: 16px;
        padding-left: 20px;
        font-size: 14px;
        margin-bottom: 21px;
    }
    &__link {
        margin-bottom: 12px;
    }
}

.progressbar {
    &-bottom {
        display: flex;
        align-items: center;
        justify-content: space-between;
        &__desc {
            display: flex;
            align-items: center;
            font-size: 12px;
            text-transform: uppercase;
            svg {
                display: block;
                padding-right: 6px;
                width: 14px;
                height: 14px;
                fill: var(--color-white);
            }
        }
    }
}

.progressbar__line {
    display: block;
    position: relative;
    width: 100%;
    height: 12px;
    overflow: hidden;
    background-color: rgba(#000, 0.21);
    border-radius: 12px;
    margin-bottom: 12px;
    &::-webkit-progress-bar {
        background-color: rgba(#000, 0.21);
        border-radius: 12px;
    }
    &::-webkit-progress-value {
        background-color: #2EE1CE;
        border-radius: 12px;
    }
    &::-moz-progress-bar {
        background-color: #2EE1CE;
        border-radius: 12px;
    }
}