@media (max-width: 1600px) {
    .hero-stages {
        &__container {
            padding-top: 90px;
            padding-bottom: 72px;
        }
    }
}
@media (max-width: 1366px) {
    .container {
        max-width: 1224px;
    }
    .logo {
        font-size: 18px;
    }
    .header {
        &__container {
            padding-left: var(--container-gutter);
            padding-right: var(--container-gutter);
            max-width: 1224px;
            margin: 0 auto;
        }
    }
    .hero {
        &-stages {
        }
        &-stage {
            &__item {
                grid-template-columns: repeat(10, 1fr);
            }
            &__title {
                max-width: 320px;
            }
            &__left {
                grid-column: 4 span;
            }
            &__right {
                grid-column: 4 span;
            }
        }
    }
    .stages {
        display: grid;
        padding-left: vw(70);
        padding-right: vw(70);
        gap: 40px;
        grid-template-columns: repeat(12, 1fr);
        &__tabs {
            &-btn {
                padding: 18px 40px;
            }
        }
        &__container {
            margin: 0;
            padding: 0;
            max-width: none;
            grid-column: 10 span;
        }
    }
    .nav {
        position: relative;
        grid-column: 2 span;
        left: 0;
        top: 30px;
        &__list {
            grid-template-columns: 1fr;
            gap: 16px;
        }
        &__link {
            padding: 25px;
            font-size: 10px;
            svg {
                width: 29px;
                height: 29px;
            }
        }
    }
    .stage {
        &__bottom {
            display: none;
            border-radius: 6px;
            overflow: hidden;
            gap: 0px;
            flex-direction: column;
            &.show {
                display: flex;
            }
        }
        &__left {
            align-items: flex-start;
            width: calc(100% - 65px);
            column-gap: 30px;
            display: grid;
            padding: 40px 32px;
            border-radius: 0px;
            grid-template-columns: repeat(2, 1fr);
            box-shadow: inset 0px -1px 0px #EBEFF2;
            &-info {
                justify-content: flex-start;
                grid-area: 3;
                &__image {
                    margin-right: 12px;
                    margin-bottom: 0;
                    svg {
                        width: 40px;
                        height: 40px;
                    }
                }
                &__item {
                    padding: 0;
                    margin-right: 27px;
                    flex-direction: row;
                    &:first-child {
                        border: none;
                    }
                }
                &__desc {
                    font-size: 14px;
                    line-height: 20px;
                    white-space: normal;
                    max-width: 65px;
                }
            }
            &-members {
                grid-area: 3;
            }
        }
        &__right {
            box-shadow: inset 0px -1px 0px #EBEFF2;
            border-radius: 0px;
            width: calc(100% - 65px);
        }
        &__progressbar {
            grid-area: 4;
            &:not(:last-child) {
                margin-bottom: 0;
            }
            .progressbar__line {
                margin-bottom: 0;
            }
            &-bottom {
                margin-bottom: 10px;
            }
        }
        &__author {
            margin-bottom: 30px;
        }
    }
}
@media (max-width: 1024px) {
    .logo {
        font-size: 15px;
    }
    .header {
        &__container {
            padding-top: 17px;
            padding-bottom: 17px;
        }
        &-actions {
            margin-right: 20px;
        }
    }
    .hero-stage {
        &__item {
            column-gap: 23px;
        }
        &__image {
            grid-column: 1 span;
            max-width: 96px;
            padding: 29px 6px;
        }
        &__left {
            grid-column: 9 span;
            margin-bottom: 30px;
        }
        &__title {
            font-size: 20px;
            max-width: none;
            margin-bottom: 20px;
        }
        &__desc {
            margin-bottom: 18px;
        }
        &__right {
            align-items: center;
            flex-direction: row;
            grid-column: 10 span;
            margin-left: vw(240);
            padding-top: 0;
        }
        &__link {
            margin-bottom: 0;
        }
        &__info {
            gap: vw(46);
            &-name {
                white-space: normal;
            }
            
            &-desc {
                white-space: normal;
            }
        }
    }
    .progressbar {
        &__line {
            height: 8px;
        }
    }
    .stages {
        padding: 0 25px 64px 25px;
        &__tabs {
            &-btn {
                padding: 18px 30px;
            }
        }
    }
    .nav {
        top: 0;
        padding: 100px 0 64px;
        &__link {
            padding: 15px;
            font-size: 9px;
        }
    }
    .stage {
        &__left {
            &-top {
                grid-column: 2 span;
            }
            &-info {
                grid-area: 2 span;
                align-items: flex-start;
                flex-direction: column;
                margin-bottom: 28px;
            }
            &-members {
                grid-area: 1 span;
                margin-bottom: 28px;
            }
        }
        &__author {
            grid-area: 1 span;
            margin-bottom: 0;
        }
        &__dropdown {
            right: 70px;
            &:before {
                right: 12px;
                left: auto;
                transform: translateX(0);
            }
        }
        &__item {
            position: relative;
            &-top {
                margin-bottom: 50px;
            }
            &-body {
                position: absolute;
                bottom: 24px;
                right: 0;
            }
            &-footer {
                padding-bottom: 10px;
            }
        }
    }
}
@media (max-width: 768px) {
    .burger {
        display: flex;
    }
    .header {
        &__container {
            align-items: center;
        }
    }
    
    .logo {
        font-size: 20px;
    }
    .hero-stages {
        &__container {
            padding-top: 84px;
            padding-bottom: 54px;
        }
    }
    .hero-stage {
        &__title {
            margin-bottom: 12px;
        }
        &__left {
            margin-bottom: 30px;
        }
        &__right {
            margin-left: 0;
        }
        &__info {
            gap: 25px;
        }
    }
    .stages {
        display: block;
        position: static;
        padding-bottom: 60px;
        &__tabs {
            margin-right: -30px;
            &-btn {
                padding: 18px 25px;
            }
        }
        &__container {
            grid-column: 12 span;
        }
    }
    .nav {
        opacity: 0;
        visibility: hidden;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: red;
        padding: 93px 0 30px;
        &__link {
            padding: 14px 11px;
        }
    }
    .stage {
        &__progressbar {
            grid-area: 2 span;
            grid-column: 2 span;
            &:not(:last-child) {
                margin-bottom: 30px;
            }
        }
    }
}
@media (max-width: 670px) {
    .hero-stage {
        &__right {
            display: flex;
            flex-direction: column;
        }
        &__link {
        }
        &__info {
            display: flex;
            width: 100%;
            justify-content: space-between;
            margin-bottom: 30px;
            &-image {
                padding: 6px;
            }
        }
    }
    .stage {
        &__item {
            display: grid;
            &-body {
                position: static;
                align-items: center;
                justify-content: space-between;
                grid-area: 3;
            }
            &-footer {
                padding-bottom: 0;
                margin-bottom: 16px;
            }
            &-author {
                margin-right: 0;
            }
        }
    }
}
@media (max-width: 576px) {
    .header {
        &__container {
            padding-top: 70px;
        }
        &-actions {
            display: none;
        }
    }
    .logo {
        font-size: 24px;
    }
    .user {
        padding: 12px;
        &__icon {
            margin-right: 3px;
        }
    }
    .hero-stages {
        &__container {
            padding-top: 162px;
            padding-bottom: 70px;
        }
    }
    .progressbar {
        &__line {
            margin-bottom: 21px;
        }
    }
    .hero-stage {
        &__image {
            width: 136px;
            padding: 49px 26px;
            margin-bottom: 52px;
        }
        &__left {
            margin-bottom: 0;
        }
        &__title {
            margin-bottom: 24px;
        }
        &__map  {
            display: none;
        }
    }
    .stages {
        &__tabs {
            &-btn {
                font-size: 16px;
                padding: 24px 25px;
            }
        }
    }
    .stage {
        &__left {
            display: flex;
            flex-direction: column;
            padding: 31px 23px;
            gap: 0px;
            width: calc(100% - 46px);
            &-top {
                width: 100%;
                .more {
                    display: none;
                }
            }
            &-info {
                width: 100%;
                flex-direction: row;
                justify-content: space-between;
                margin-bottom: 16px;
                &__item {
                    margin-right: 0;
                }
            }
            &-members {
                width: 100%;
            }
        }
        &__author {
            width: 100%;
            margin-bottom: 16px;
        }
        &__progressbar {
            width: 100%;
        }
    }
    .author {
        &__name {
            max-width: 90px;
        }
    }
}
@media (max-width: 500px) {
    .container {
        padding-left: 15px;
        padding-right: 15px;
    }
    .header {
        &__container {
            padding-left: 15px;
            padding-right: 15px;
            padding-top: 50px;
        }
    }
    .hero-stages {
        &__container {
            padding-top: 130px;
            padding-bottom: 57px;
        }
    }
    .user {
        padding: 6px;
        &__icon {
            margin-right: 0;
        }
        &__arrow {
            display: none;
        }
    }
    .logo {
        font-size: 20px;
    }
    .hero-stage {
        &__image {
            margin-bottom: 20px;
        }
        &__item {
            display: flex;
            align-items: center;
            flex-direction: column;
        }
        &__title {
            text-align: center;
            line-height: 27px;
        }
        &__right {
            width: 100%;
        }
        &__info {
            justify-content: space-between;
            &-item {
                text-align: center;
                flex-direction: column;
            }
            &-image {
                padding: 4px;
                margin-bottom: 10px;
                svg {
                    width: 24px;
                    height: 24px;
                }
            }
            &-name {
                font-size: 18px;
                margin-bottom: 3px;
            }
            &-desc {
                font-size: 14px;
            }
        }
        &__progressbar {
            margin-bottom: 30px;
        }
    }
    .user {
        &-dropdown {
            &:before {
                right: 17px;
            }
        }
    }
    .stages {
        padding: 0;
        padding-bottom: 64px;
    }
}
@media (max-width: 360px) {
    .hero-stage {
        &__info {
            &-name {
                font-size: 16px;
            }
            &-desc {
                font-size: 12px;
            }
        }
    }
    .stage {
        &__left {
            &-info {
                &__image {
                    padding: 3px;
                }
                &__desc {
                    line-height: 15px;
                    max-width: 50px;
                    font-size: 12px;
                }
            }
            &-members {
                &__image {
                    width: 32px;
                    height: 32px;
                    &:not(:first-child) {
                        margin-left: -10px;
                    }
                }
                &__more {
                    font-size: 10px;
                    width: 32px;
                    height: 32px;
                }
            }
        }
    }
}