.nav {
    position: absolute;
    top: 80px;
    left: vw(72);
    color: var(--color-violent-gray);
    &__list {
        display: grid;
        gap: 20px;
        grid-template-columns: repeat(2, 1fr);
    }
    &__link {
        display: flex;
        flex-direction: column;
        align-items: center;
        text-transform: uppercase;
        border-radius: 6px;
        font-size: 11px;
        padding: 19px;
        transition: background-color 0.3s, color 0.3s;
        svg {
            display: block;
            width: 32px;
            height: 32px;
            margin-bottom: 10px;
            fill: var(--color-violent-gray);
            transition: fill 0.3s;
        }
        &:hover {
            color: var(--color-white);
            background-color: var(--color-accent);
            svg {
                fill: var(--color-white);
            }
        }
    }
}